<template>
  <div class="page">
    <div class="header">
      <div class="title">Work Orders</div>
      <div class="actions">
        <a-tag color="blue">Role: {{ isAdmin ? 'admin' : 'user' }}</a-tag>
        <a-button @click="onLogout" size="small">Logout</a-button>
      </div>
    </div>

    <a-row :gutter="16">
      <a-col :span="14">
        <a-card title="Work Orders List">
          <a-table :columns="columns" :data-source="dataSource" :pagination="false" row-key="id">
            <template #bodyCell="{ column, record }">
              <template v-if="column.key === 'overtime'">
                <a-tag :color="record.overtime ? 'red' : 'green'">{{ record.overtime ? 'Yes' : 'No' }}</a-tag>
              </template>
              <template v-else-if="column.key === 'actions'">
                <template v-if="isAdmin">
                  <a-popconfirm title="Delete this work order?" ok-text="Yes" cancel-text="No" @confirm="() => onDelete(record.id)">
                    <a-button type="link" danger>Delete</a-button>
                  </a-popconfirm>
                </template>
                <template v-else>-</template>
              </template>
              <template v-else>
                {{ record[column.dataIndex] }}
              </template>
            </template>
          </a-table>
        </a-card>
      </a-col>
      <a-col :span="10">
        <a-card title="Project Hours Distribution">
          <div ref="chartRef" class="chart"></div>
        </a-card>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import { computed, onMounted, onBeforeUnmount, ref, watch } from 'vue'
import { useStore } from 'vuex'
import * as echarts from 'echarts'

export default {
  setup() {
    const store = useStore()
    const isAdmin = computed(() => store.getters.isAdmin)
    const dataSource = computed(() => store.getters.workOrders)

    const columns = [
      { title: 'ID', dataIndex: 'id', key: 'id', width: 80 },
      { title: 'Project', dataIndex: 'project', key: 'project' },
      { title: 'Overtime', dataIndex: 'overtime', key: 'overtime', width: 100 },
      { title: 'Hours', dataIndex: 'hours', key: 'hours', width: 100 },
      { title: 'Created At', dataIndex: 'created_at', key: 'created_at', width: 160 },
      { title: 'Actions', key: 'actions', width: 100 }
    ]

    const chartRef = ref(null)
    let chartInstance = null

    const renderChart = () => {
      const map = store.getters.hoursByProject
      const categories = Object.keys(map)
      const values = Object.values(map)
      if (!chartInstance && chartRef.value) {
        chartInstance = echarts.init(chartRef.value)
      }
      if (!chartInstance) return
      chartInstance.setOption({
        title: { text: 'Project Hours Distribution' },
        tooltip: {},
        xAxis: { type: 'category', data: categories },
        yAxis: { type: 'value' },
        series: [
          {
            type: 'bar',
            data: values,
            itemStyle: { color: '#1890ff' }
          }
        ],
        grid: { left: 40, right: 20, top: 50, bottom: 40 }
      })
      chartInstance.resize()
    }

    const onDelete = (id) => {
      if (!isAdmin.value) return
      store.commit('deleteWorkOrder', id)
    }

    const onLogout = () => {
      store.commit('logout')
      window.location.hash = '#/login'
    }

    onMounted(() => {
      renderChart()
      window.addEventListener('resize', renderChart)
    })

    onBeforeUnmount(() => {
      window.removeEventListener('resize', renderChart)
      if (chartInstance) {
        chartInstance.dispose()
      }
    })

    watch(
      () => store.getters.hoursByProject,
      () => {
        renderChart()
      },
      { deep: true }
    )

    return { columns, dataSource, onDelete, isAdmin, onLogout, chartRef }
  }
}
</script>

<style lang="less" scoped>
.page {
  padding: 16px;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 12px;
}
.title {
  font-size: 18px;
  font-weight: 600;
}
.chart {
  width: 100%;
  height: 360px;
}
</style>


